<template>
    <!-- 新增企业招聘 -->
    <div class="recruit">
        <navigate title="企业招聘" position="absolute">
            <mu-button slot="right" color="#fff" @click="handlerSubmit" flat>发送</mu-button>
        </navigate>
        <div class="scroll-wrapper">
            <!-- 输入框 -->
            <div class="input-wrapper border-1px-b" style="padding: 0 20px 30px">
                <mu-text-field v-model="content" placeholder="企业招聘"
                               :rows="20"
                               :rows-max="20"
                               multi-line
                               full-width
                               solo/>
            </div>
            <!-- 输入框 -->
        </div>
    </div>
    <!-- 新增企业招聘 -->
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import {saveMoment} from "../../api/dynamic"

    export default {
        name: "recruit",
        data() {
            return {
                content: ''
            }
        },
        activated() {
            this.content = ''
        },
        methods: {
            async handlerSubmit() {
                const loading = this.$loading();

                if (!this.content) {
                    return this.$toast('请填写招聘信息!')
                }
                const data = {
                    type: '004005',
                    mediaType: '003002',
                    status: '020002',
                    content: this.content
                }

                await saveMoment(data)

                loading.close()

                this.$toast('发表成功！')

                this.$emit('refresh')

                this.$router.go(-2)
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .recruit
        padding-top 46px
        background-color #fff
        .appbar
            position absolute
            top 0
            left 0
            z-index 1
            width 100%
            height 46px
            box-shadow none
            background-color #3899D7
            .title
                color #fff
                line-height 46px
                text-align center
                font-size 15px
        .scroll-wrapper
            width 100%
            height 100%
            .form
                .form-item-s
                    margin 0
                    padding 10px
                    .mu-form-item-help
                        bottom 5px
                .form-item-buttons
                    padding 20px 0
                    .button
                        color #fff
                        background-color #2196f3
                        &.clear
                            color #999
                            background-color #fff
                .button-wrapper
                    padding 20px 0

</style>



